import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import outpatient from '@/components/outpatient'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'outpatient',
      component:outpatient
    }
  ]
})



